import {useContext, useState} from "react"
import EventComponent from "./component/Event";
import {Box, Button, Tab, Tabs, Typography} from "@mui/material";
import {StatsContext} from "./context/StatsContext";
import Events from "./model/Events";
import Header from "./component/Header";

const App = () => {
    const {
        notorietyState: [, setNoto],
        popularityState: [, setPop],
        moneyState: [, setMoney],
        privateLifeState: [, setLife],
    } = useContext(StatsContext);
    const [index, setIndex] = useState(0)
    const end = index >= Events.length;
    const [tab, setTab] = useState(0)
    const [select, setSelect] = useState(false)

    return (
        <>
            <Header end={end}/>
            <Box sx={{
                p: 1,
                width: 1.0,
                display: "flex",
                flexWrap: "wrap",
                justifyContent: "center",
                maxWidth: 500,
                margin: "0 auto"
            }}>


                {!select &&
                    <>
                        <Box width={1.0} marginBottom={1}>
                            <Tabs variant="fullWidth" value={tab} onChange={(e, v) => setTab(v)}>
                                <Tab label="Sam Salamanca"/>
                                <Tab label="Ann Sunshine"/>
                                <Tab label="Melody Mysterious"/>
                                <Tab label="Flynn Frster"/>
                            </Tabs>
                        </Box>
                        <Box>
                            {tab === 0 && <>
                                <Typography>            .
                                    .       .     
                                      !    -    !
                                    ,       '.     
                                            .
                                    .</Typography>
                            </>}
                            {tab === 1 && <>
                                <Typography> -                                          .   
                                    ,                                         .                                ,     
                                     . ,                                        
                                      .</Typography>
                            </>}
                            {tab === 2 && <>
                                <Typography>                                           , ,   
                                      .                                           ,  
                                                                            .   , 
                                         .                                       !</Typography>
                            </>}
                            {tab === 3 && <>
                            <Typography>        U21                                  U21. 
                                       .                                                                   '       .                                   .  ,                                      .                                      
                                     
                                    .</Typography>
                            </>}
                            <Box p={1} display={"flex"} justifyContent={"center"}>
                                <Button variant={"contained"} onClick={() => setSelect(true)}></Button>
                            </Box>
                                <img src={`./${tab + 1}.png`} alt="" style={{width: "100%"}}/>

                        </Box>
                    </>
                }

                {select &&
                    <>
                        {!end && <EventComponent event={Events[index]} next={() => setIndex(index + 1)}/>}
                        {end && <>
                            <Box
                                sx={{
                                    width: 1.0,
                                    display: "flex",
                                    alignItems: "center",
                                    flexDirection: "column",
                                    gap: 4,
                                    p: 4
                                }}>
                                <Typography variant={"h3"}>?</Typography>
                                <Typography><Button variant={"outlined"} onClick={() => {
                                    setIndex(0)
                                    setNoto(0)
                                    setPop(0)
                                    setMoney(0)
                                    setLife(0)
                                }}> .</Button></Typography>
                            </Box>
                        </>}
                    </>

                }


            </Box>
        </>
    );
};

export default App;
